.wp-block-miscellaneous-gutenberg-blocks-flexbox {
	display: flex;
	width: 100%;
	@media only screen and (min-width: 993px) {
		&.miscellaneous-gutenberg-blocks-flexbox--wrap {
			flex-wrap: wrap;
		}

		&.miscellaneous-gutenberg-blocks-flexbox--horizontal {
			flex-direction: row;
			&.miscellaneous-gutenberg-blocks-flexbox--reverse {
				flex-direction: row-reverse;
			}
		}
		&.miscellaneous-gutenberg-blocks-flexbox--vertical {
			flex-direction: column;
			&.miscellaneous-gutenberg-blocks-flexbox--reverse {
				flex-direction: column-reverse;
			}
		}

		&.miscellaneous-gutenberg-blocks-flexbox--justify-flex-start {
			justify-content: flex-start;
		}

		&.miscellaneous-gutenberg-blocks-flexbox--justify-flex-end {
			justify-content: flex-end;
		}

		&.miscellaneous-gutenberg-blocks-flexbox--justify-center {
			justify-content: center;
		}

		&.miscellaneous-gutenberg-blocks-flexbox--justify-space-between {
			justify-content: space-between;
		}

		&.miscellaneous-gutenberg-blocks-flexbox--justify-space-around {
			justify-content: space-around;
		}

		&.miscellaneous-gutenberg-blocks-flexbox--align-flex-start {
			align-items: flex-start;
		}

		&.miscellaneous-gutenberg-blocks-flexbox--align-flex-end {
			align-items: flex-end;
		}

		&.miscellaneous-gutenberg-blocks-flexbox--align-baseline {
			align-items: baseline;
		}

		&.miscellaneous-gutenberg-blocks-flexbox--align-center {
			align-items: center;
		}

		&.miscellaneous-gutenberg-blocks-flexbox--align-stretch {
			align-items: stretch;
		}

		&.miscellaneous-gutenberg-blocks-flexbox--align-none {
			align-items: initial;
		}
	}
	@media only screen and (min-width: 601px) and (max-width: 992px) {
		&.miscellaneous-gutenberg-blocks-flexbox--tablet-wrap {
			flex-wrap: wrap;
		}
		&.miscellaneous-gutenberg-blocks-flexbox--tablet-horizontal {
			flex-direction: row;
			&.miscellaneous-gutenberg-blocks-flexbox--tablet-reverse {
				flex-direction: row-reverse;
			}
		}
		&.miscellaneous-gutenberg-blocks-flexbox--tablet-vertical {
			flex-direction: column;
			&.miscellaneous-gutenberg-blocks-flexbox--tablet-reverse {
				flex-direction: column-reverse;
			}
		}

		&.miscellaneous-gutenberg-blocks-flexbox--justify-tablet-flex-start {
			justify-content: flex-start;
		}

		&.miscellaneous-gutenberg-blocks-flexbox--justify-tablet-flex-end {
			justify-content: flex-end;
		}

		&.miscellaneous-gutenberg-blocks-flexbox--justify-tablet-center {
			justify-content: center;
		}

		&.miscellaneous-gutenberg-blocks-flexbox--justify-tablet-space-between {
			justify-content: space-between;
		}

		&.miscellaneous-gutenberg-blocks-flexbox--justify-tablet-space-around {
			justify-content: space-around;
		}

		&.miscellaneous-gutenberg-blocks-flexbox--align-tablet-flex-start {
			align-items: flex-start;
		}

		&.miscellaneous-gutenberg-blocks-flexbox--align-tablet-flex-end {
			align-items: flex-end;
		}

		&.miscellaneous-gutenberg-blocks-flexbox--align-tablet-baseline {
			align-items: baseline;
		}

		&.miscellaneous-gutenberg-blocks-flexbox--align-tablet-center {
			align-items: center;
		}

		&.miscellaneous-gutenberg-blocks-flexbox--align-tablet-stretch {
			align-items: stretch;
		}

		&.miscellaneous-gutenberg-blocks-flexbox--align-tablet-none {
			align-items: initial;
		}
	}
	@media only screen and (max-width: 600px) {
		&.miscellaneous-gutenberg-blocks-flexbox--mobile-wrap {
			flex-wrap: wrap;
		}
		&.miscellaneous-gutenberg-blocks-flexbox--mobile-horizontal {
			flex-direction: row;
			&.miscellaneous-gutenberg-blocks-flexbox--mobile-reverse {
				flex-direction: row-reverse;
			}
		}
		&.miscellaneous-gutenberg-blocks-flexbox--mobile-vertical {
			flex-direction: column;
			&.miscellaneous-gutenberg-blocks-flexbox--mobile-reverse {
				flex-direction: column-reverse;
			}
		}

		&.miscellaneous-gutenberg-blocks-flexbox--justify-mobile-flex-start {
			justify-content: flex-start;
		}

		&.miscellaneous-gutenberg-blocks-flexbox--justify-mobile-flex-end {
			justify-content: flex-end;
		}

		&.miscellaneous-gutenberg-blocks-flexbox--justify-mobile-center {
			justify-content: center;
		}

		&.miscellaneous-gutenberg-blocks-flexbox--justify-mobile-space-between {
			justify-content: space-between;
		}

		&.miscellaneous-gutenberg-blocks-flexbox--justify-mobile-space-around {
			justify-content: space-around;
		}

		&.miscellaneous-gutenberg-blocks-flexbox--align-mobile-flex-start {
			align-items: flex-start;
		}

		&.miscellaneous-gutenberg-blocks-flexbox--align-mobile-flex-end {
			align-items: flex-end;
		}

		&.miscellaneous-gutenberg-blocks-flexbox--align-mobile-baseline {
			align-items: baseline;
		}

		&.miscellaneous-gutenberg-blocks-flexbox--align-mobile-center {
			align-items: center;
		}

		&.miscellaneous-gutenberg-blocks-flexbox--align-mobile-stretch {
			align-items: stretch;
		}

		&.miscellaneous-gutenberg-blocks-flexbox--align-mobile-none {
			align-items: initial;
		}
	}
}
